import React from 'react';
import './subHeader.css';
import MenuOutlinedIcon from '@material-ui/icons/MenuOutlined';

function SubHeader() {
    return (
        <div className="container-fluid p-0 m-0">
            <nav className="navbar navbar-expand-sm navbar-dark p-0 m-0 subNavbar">
                <div className="menuContainer text-light">
                    <MenuOutlinedIcon className="menuIcon"/>
                    <span className='subHeaderMenuIconLable'>All</span>
                </div>
                <div className="d-flex subHeaderOptions">
                     <div className="navbarLinks text-light subHeaderListItems">
                        <span>Mobiles</span>
                        <span>Best Sellers</span>
                        <span>Today's Deals</span>
                        <span>Prime</span>
                        <span>Computers</span>
                        <span>New Releases</span>
                        <span>Amazon Pay</span>
                        <span>Books</span>
                        <span>Gift Ideas</span>
                        <span>Customer Service</span>
                        <span>Sell</span>
                        <span>Baby</span>
                    </div>
                </div>
                <div className="rightSideImg">
                    <img src="https://images-eu.ssl-images-amazon.com/images/G/31/img20/Events/jupiter20/GWphase2/v1/P2-SWM_400x39._CB417539308_.jpg" className="img-fluid subHeaderRightImg"/>
                </div>
            </nav>
        </div>
    )
}

export default SubHeader;
